$loader-size: 12px;
$loader-color: $grey-dark-light;

@mixin loader($color: $loader-color) {
  content: '';
  position: absolute;
  display: block;
  left: calc(50% - (#{$loader-size}/2));
  top: calc(50% - (#{$loader-size}/2));
  height: $loader-size;
  width: $loader-size;
  border: 2px solid $color;
  border-radius: 50%;
  border-right-color: transparent;
  border-top-color: transparent;
  animation: spinAround .5s infinite linear;
}

@mixin centerAbsolute {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

@mixin pulseLoader($size: $loader-size) {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    display: block;
    width: $size;
    height: $size;
    border-radius: 50%;
    background-color: $blue;
    animation: spinBounce 1s 0s ease-in-out infinite;
  }
}