@mixin tabPaneLoader {
  position: relative;
  min-height: 224px;
  background: radial-gradient(rgba(236, 241, 255, .5), transparent);
  margin-top: 0;

  * {
    display: none;
  }

  &:before {
    content: 'Carregando...';
    top: calc(50% + 12px);
    width: 100%;
    text-align: center;
    position: absolute;
    font-size: $size-text-small;
    color: $color-link;
  }

  &:after {
    @include loader($color-link);
  }
}

.tabs {
  display: flex;
  list-style: none;
  margin: 0;
  border-bottom: 1px solid $grey
}

.tab {
  padding: 10px 16px;
  border: 1px solid transparent;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  margin-bottom: -1px;
  cursor: pointer;

  a {
    color: $color-link;
    text-decoration: none;
  }

  & + .tab {
    margin-left: 2px;
  }

  &:hover:not(.-disabled):not(.-active),
  &:focus:not(.-disabled):not(.-active) {
    background-color: #f5f5f5;
    border-bottom: 1px solid $grey
  }

  &.-active {
    border-color: $grey $grey #fff;

    a {
      color: $color-text;
    }
  }

  &.-disabled {
    cursor: default;

    a { 
      color: $color-disabled;
      cursor: inherit
    }
  }

  &.-pulse {
    @include pulseLoader();

    &:before {
      top: 6px;
      right: 6px;
    }
  }
  
}

.tab-panes {
  &.-loading {
    .tab-pane {
      @include tabPaneLoader()
    }
  }
}

.tab-pane {
  margin-top: $space-s;
  overflow-y: auto;

  &:not(.-active) {
    display: none;
  }

  &.-loading {
    @include tabPaneLoader()
  }
}