$switch-width: 44px;
$switch-height: 22px;
$switch-marker-size: 18px;

.switch {
  position: relative;
  width: $switch-width;
  height: $switch-height;
  border-radius: $switch-height;
  background-color: #ccc;
  box-sizing: border-box;
  outline: none;

  label {
    color: transparent;
    user-select: none;
  }

  label > input[type=checkbox] {
    position: absolute;
    left: 50%;

    &:focus + .switch-marker:before {
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075),
                0 0 1px 2px rgba(82, 168, 236, .5);
    }

    &:checked + .switch-marker {

      &:after {
        transform: translateX(calc(100% + 4px));
      }

      &:before {
        background-color: $blue;
      }
    }
  }
}

.switch-marker {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  
  &:before {
    content: '';
    display: block;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #ccc;
    border-radius: $switch-height;
    transition: box-shadow .2s linear;
  }


  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    width: $switch-marker-size;
    height: $switch-marker-size;
    border-radius: 50%;
    background-color: #fff;
    transition: transform .2s ease-in-out, width .2s ease-in-out;
  }
}