// Disclaimer: Nível de Especificidade
//
// utilizado o seletor `table.form-table` e
// `tbody tr td` para sobrescrever as propriedades
// default do Orquestra, incrementando a especificidade
// necessária.

table.form-table {
  min-width: 100%;
  border: none;
  margin-bottom: 48px;
  // table-layout: fixed;

  tbody tr {
    border: none; // reset execute.css tr border style
  }

  tr.header td {
    line-height: 24px;
    vertical-align: middle;
    border-top: 1px solid $grey;
    border-bottom: 1px solid $grey;
  }

  tbody tr td {
    border: none;
    padding: 12px 10px;
    vertical-align: top;
  }

  tr:not(.header) td:first-child button {
    margin-top: 3px;
  }

  input,
  select {
    margin-bottom: 0px
  }

  &[mult=S].-static {
    td:first-child * {
      display: none;
    }
  }

  &.-readonly {
    td {
      vertical-align: top;
    }

    td:first-child {
      padding-left: 74px + 10px;
    }
    
    .form-input-icon {
      display: none;
    }
  }

  &.-line-height {
    td {
      line-height: 30px;
    }
  }

  &.-hover {
    tr:not(.header):hover {
      background-color: $color-table-hover;
    }
    margin-bottom: 0;
  }

  &.-footer:not(.-hover):not(.-readonly) {
    margin-bottom: 3px;
  }
}

tfoot.form-table-footer {
  td {
    padding: 12px 10px;
    border-top: 1px solid $grey;
    border-bottom: 1px solid $grey;
    background-color: #f5f5f5;
    font-weight: 500;
    color: #303030;
  }
}