// Table Collapse

$table-collapse-cell-padding: 14px;
$table-collapse-th-height: 48px;
$table-collapse-th-height-summary: 70px;

.table-collapse {
  position: relative;
  min-width: 100%;

  td, th {
    box-sizing: border-box;
    padding: $table-collapse-cell-padding;
    text-align: left;
  }

  th {
    border-bottom: none; // reset global.css th border-bottom style
  }

  thead th {
    background-color: transparent;
    border-bottom: 1px solid $grey;
  }

  tbody th {
    text-align: left;
  }

  .-stick {
    td, th {
      position: sticky;
    }

    &:not(.table-collapse-header) {
      th {
        box-shadow: 0 -1px 0 0 #fff, 0 1px 0 0 $grey;
        z-index: 1;
      }

      td, th {
        top: 0;
        background-color: #fff;
        border-top: 1px solid #fff;
        border-bottom: 1px solid #fff;
      }
    }
  }

  &.-tree tbody:not(.table-collapse-header) td:first-child {
    padding-left: $table-collapse-cell-padding * 2;
  }

  &.-padded tr {
    td:first-child,
    th:first-child {
      padding-left: $space-l;
    }
  }

  &.-padded.-tree tbody {
    &:not(.table-collapse-header) td:first-child {
      padding-left: $space-l + $table-collapse-cell-padding * 3;
    }
  }

  &.-hover {
    tr:hover {
      background-color: $color-table-hover;
    }
  }
}

.table-collapse-header {

  th {
    background-color: #f5f5f5;

    &:first-child {
      white-space: nowrap;
    }
  }

  th:first-child {
    cursor: pointer;
    
    &:hover {
      text-decoration: underline;
    }
  }

  &.-stick {
    th {
      top: $table-collapse-th-height;
    }

    &:not(.-collapse) th:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 5px;
      left: 0;
      bottom: -5px;
      background: rgba(255, 255, 255, 0);
      background: linear-gradient(rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, .05) 30%, transparent 100%)
    }
  }

  &.-stick.-summary {
    th {
      top: $table-collapse-th-height-summary;
    }
  }

  &.-collapse {
    .table-collapse-indicator {
      transform: rotate(-90deg);
    }


    & + .table-collapse-group {
      display: none;
    }
  }
}

.table-collapse-indicator {
  margin-right: $space-xs;
  font-size: $size-text-small;
}

.th-sup {
  font-size: 9px;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: $color-disabled;
  font-weight: 400;
  position: absolute;
  top: -6px;
  right: $table-collapse-cell-padding;
}