.btn {

  &.-loading {
    color: transparent !important;
    text-shadow: none !important;
    position: relative;
    pointer-events: none;
    user-select: none;

    &:after {
      @include loader;
    }

    &[class*='btn-']:after {
      border-left-color: rgba(255, 255, 255, .75);
      border-bottom-color: rgba(255, 255, 255, .75);
    }
  }
}