.tag {
  font-size: $size-text-small;
  background-color: #eaeaea;
  color: $grey-dark;
  font-weight: 500;
  padding: 3px 6px;
  border-radius: 4px;

  &.-success {
    background-color: #e3f8eb;
    color: #317046;
  }

  &.-danger {
    background-color: #fee5e4;
    color: #8b3e3a
  }

  &.-warning {
    background-color: #fff4e5;
    color: #825e2a;
  }
}