$form-help-height: 24px;

.form-section {
  *
  *:after,
  *:before {
    box-sizing: border-box;
  }

  font-size: $size-text-base;
  border-bottom: 1px solid $grey;
  overflow-x: hidden;

  &:not(.-gapless) {
    padding-bottom: $space-m;
  }

  &.-space {
    padding-top: $space-m;
  }

  // color: $grey-darkest; ou colocar o 6a6a6a do próprio Orquestra
}

.form-container {
  padding: 0 $space-l 0 $space-l;

  &.-inline {
    .form-group {
      position: relative;
    }

    .form-help {
      position: absolute;
    }
  }
}

.form-title {
  font-size: $size-title;
  font-weight: bold;
  color: $grey-darkest;
  margin-bottom: $space-m;
  margin-top: $space-xs;

  > h1 {
    display: inline-block;
    font-size: inherit;
    font-weight: inherit;
    color: inherit;
    margin-bottom: 0;
  }

  > p {
    font-size: 12px;
    font-weight: 400;
    color: $grey-dark;
  }
}

.form-title-icon {
  display: inline-block;
  margin-right: $space-xs;
  color: $blue;
  font-size: $size-title-icon;
}

.form-title-tagline {
  display: block;
  color: $grey-dark;
  font-weight: normal;
  font-size: $size-title-tagline;
  margin-bottom: $space-xxs;

  &.-double {
    min-height: 40px;
  }
}

.form-group {
  & + .form-group {
    margin-top: $space-s;
  }

  &.-error {
    .form-input {
      .input-group-addon {
        border-color: $color-error;
      }

      input,
      select,
      textarea {
        border-color: $color-error;

        &:focus {
          // override default bootstrap :focus border-color and box-shadow
          border-color: $color-error-focus !important;
          box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px $color-error-focus-shadow !important;
        }
      }
    }

    .form-help {
      color: $color-error;
    }
  }
}

.form-label {
  font-size: $size-text-base !important; // override global.css label style
  color: $grey-darkest;
  font-weight: 500;
  white-space: nowrap;
  cursor: text;

  &.-bold {
    font-weight: 600;
  }
}

.form-text {
  position: relative;
  display: block;
  color: $grey-dark;
  margin: 0;
}

.form-input {
  position: relative;
  line-height: 30px;
  height: 30px;
  color: $grey-dark;

  &.-date {
    max-width: 120px;
  }

  &.-icon-right:not(.input-group) {
    > input {
      padding-right: 24px;
    }

    .form-input-icon {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      color: $grey-dark-light;
    }
  }
}

.form-input,
.form-table {
  input[type=text],
  select,
  textarea {
    margin-bottom: 0; // override bootstrap style
    width: 100% !important;
    box-sizing: border-box;
  }

  input[type=text],
  select {
    height: 30px;
  }
}

.form-help {
  font-size: $size-text-small;
  margin: 0;
  line-height: $form-help-height;
  max-width: 480px;

  &.-long {
    margin-top: 3px;
    line-height: 1.6;
  }
}
