//
// Colors
//
.color-red {
  color: $red;
}

.color-blue {
  color: $blue;
}

.color-green {
  color: $green;
}

.color-orange {
  color: $orange;
}

.uppercase {
  text-transform: uppercase;
}

.nowrap {
  white-space: nowrap;
}

.rotate-90 {
  transform: rotate(90deg);
}

.text-small {
  font-size: $size-text-small;
}

.text-summary {
  font-size: $size-text-small;
  font-weight: 400;
  color: $color-disabled;
}

.text-sub {
  font-size: $size-text-small;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: $color-disabled;
  font-weight: 400;
  margin: 0 $space-xs;
}

.margin-left-s {
  margin-left: $space-s;
}

.margin-left-m {
  margin-left: $space-m;
}

.margin-left-l {
  margin-left: $space-l;
}

.margin-bottom-m {
  margin-bottom: $space-m;
}

.margin-bottom-l {
  margin-bottom: $space-l;
}

.margin-right-s {
  margin-right: $space-s;
}

.margin-right-m {
  margin-right: $space-m;
}

.margin-right-l {
  margin-right: $space-l;
}

.margin-top-m {
  margin-top: $space-m;
}

.margin-top-l {
  margin-top: $space-l;
}



.width-l {
  width: 420px;
}

.width-m {
  width: 320px;
}

.input-text-right {
  text-align: right;
  
  > input[type=text] {
    text-align: right;
  }
}