// Bloco inicial com dados do contrato e do produto
// Conteúdo disposto em duas colunas, previne um espaçamento
// exagerado entre as colunas para telas widescreen
#data-intro {
  max-width: 860px;

  @media (max-width: 620px) {
    flex-direction: column;

    .column + .column {
      margin-top: $space-m;
    }
  }
}


#exams-search-panes {

  // Tab Panes com o resultado da pesquisa
  // de levnatamento
  .tab-pane {
    max-height: 524px;
    overflow-x: auto;
  }

  // Configuração da largura da colunas das
  // tabelas presentes nos Tab Panes
  .table-collapse {
    th:first-child,
    td:first-child {
      width: 190px;
    }

    td:not(:last-child):not(:first-child),
    th:not(:last-child):not(:first-child)  {
      width: 232px;
    }
  }

  // Como Help+ tem mais colunas um
  // tamanho menor para otimizar o espaço
  #tbl-help-plus {
    td:not(:last-child):not(:first-child),
    th:not(:last-child):not(:first-child)  {
      width: 192px;
    }
  }

}

// Ícone (-) vínculado ao
// ícone de calendário,
// utilizado na empty message
.fa-calendar.-minus {
  position: relative;

  &:after {
    content: '\f056';
    position: absolute;
    color: $red;
    font-size: 18px;
    bottom: -6px;
    right: -6px;
    background-color: #fff;
    border-radius: 50%;
  }
}

// Tabela Exames Ocupacionais
// Ajuste de tamanho das colunas da tabela
// de acordo ainda com a tabela de Total de Exames
// posicionada exatamente abaixo.

#tbl-exames-total td:first-child {
  width: 50px;
}

#tbl-exames-total .col-total {
  padding-left: 14px;
}

#tbl-exams,
#tbl-exames-total {
  .col-s {
    width: 124px;
  }
  .col-md {
    width: 324px;
  }
  .col-total {
    width: 62px;
  }
}

// Botão secundário
// #customBtn_Revisar
.btn#customBtn_Revisar {
  border: 1px solid #ccc !important;
  text-shadow: none !important;
}