.radio-card {
  display: inline-block;

  > .radio {
    position: relative;
    padding: 12px 32px 12px 18px;
    margin-bottom: 0;
    font-weight: 600;
    font-size: 14px !important; // override global.min styles
    line-height: 1.2;
    color: $grey-dark;
  }

  > .radio input[type=radio] {
    margin-left: -9999px;
    
    &:focus + .radio-marker {
      box-shadow: 0 0 1px 2px rgba(82, 168, 236, .5);
    }

    &:checked + .radio-marker {
      border-color: $blue-light;
      background-color: rgba(82, 168, 236, .075);

      &:before {
        opacity: 1;
      }
    }

    &:disabled + .radio-marker {
      cursor: default;
      background-color: rgba(0, 0, 0, .045);
    }
  }

  &:not(.-row) >.radio + .radio {
    margin-top: 12px;
  }

  &.-row {
    display: flex;

    > .radio + .radio {
      margin-left: 18px
    }
  }

  &.-small {
    > .radio {
      max-width: 224px;
    }
  }
}

.radio-marker {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 1px solid $grey;
  border-radius: 4px;
  transition: box-shadow .2s linear;

  &:before {
    position: absolute;
    content: '\f058';
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    line-height: 1;
    color: $blue;
    display: block;
    top: 8px;
    right: 8px;
    opacity: 0;
  }

  &:hover {
    background-color: rgba(0, 0, 0, .025);
  }
}

.radio-card-helper {
  display: block;
  font-weight: initial;
  font-size: $size-text-small;
  line-height: 1.4;
  margin-top: $space-xxs;
}

[data-radio-option] {
  display: none;
}